.toogle{
	display: flex;
	justify-content: space-between;
	border: 3px solid var(--rosa) ;
	border-radius: 1rem;
	position: relative;
	padding: 2px;
	cursor: pointer;
}

.toogle>*{
	width: 1rem;
	height: 1rem;
	color: var(--rosa);
}

.t-button{
	border-radius: 100%;
	background: var(--rosa);
	position: absolute;
}